/* eslint-disable */
<template>
  <div class="register-container">
    <div class="register-box">
      <h2>Áî®Êà∑Ê≥®ÂÜå</h2>
      <form @submit.prevent="handleRegister">
        <div class="form-group">
          <input
            type="text"
            v-model="form.phone"
            placeholder="ËØ∑ËæìÂÖ•ÊâãÊú∫Âè∑"
            @input="validatePhone"
            required
          />
          <span class="error-message" v-if="errors.phone">{{ errors.phone }}</span>
        </div>
        <div class="form-group">
          <input
            type="password"
            v-model="form.password"
            placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å"
            @input="validatePassword"
            required
          />
          <span class="error-message" v-if="errors.password">{{ errors.password }}</span>
        </div>
        <div class="form-group">
          <input
            type="password"
            v-model="form.confirmPassword"
            placeholder="ËØ∑Á°ÆËÆ§ÂØÜÁ†Å"
            @input="validateConfirmPassword"
            required
          />
          <span class="error-message" v-if="errors.confirmPassword">{{ errors.confirmPassword }}</span>
        </div>
        <div class="form-group">
          <input
            type="date"
            v-model="form.birthday"
            placeholder="ËØ∑ÈÄâÊã©ÁîüÊó•"
            @input="validateBirthday"
            required
          />
          <span class="error-message" v-if="errors.birthday">{{ errors.birthday }}</span>
        </div>
        <button type="submit" class="submit-btn" :disabled="!isFormValid">Ê≥®ÂÜå</button>
      </form>
      <div class="links">
        <router-link to="/login">Â∑≤ÊúâË¥¶Âè∑ÔºüÁ´ãÂç≥ÁôªÂΩï</router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "RegisterPage",
  data() {
    return {
      form: {
        phone: "",
        password: "",
        confirmPassword: "",
        birthday: ""
      },
      errors: {
        phone: "",
        password: "",
        confirmPassword: "",
        birthday: ""
      }
    };
  },
  computed: {
    isFormValid() {
      return this.form.phone && 
             this.form.password && 
             this.form.confirmPassword &&
             this.form.birthday &&
             !this.errors.phone && 
             !this.errors.password &&
             !this.errors.confirmPassword &&
             !this.errors.birthday;
    }
  },
  methods: {
    validatePhone() {
      const phoneRegex = /^1[3-9]\d{9}$/;
      if (!this.form.phone) {
        this.errors.phone = "ÊâãÊú∫Âè∑‰∏çËÉΩ‰∏∫Á©∫";
      } else if (!phoneRegex.test(this.form.phone)) {
        this.errors.phone = "ËØ∑ËæìÂÖ•Ê≠£Á°ÆÁöÑÊâãÊú∫Âè∑";
      } else {
        this.errors.phone = "";
      }
    },
    validatePassword() {
      if (!this.form.password) {
        this.errors.password = "ÂØÜÁ†Å‰∏çËÉΩ‰∏∫Á©∫";
      } else if (this.form.password.length < 6) {
        this.errors.password = "ÂØÜÁ†ÅÈïøÂ∫¶‰∏çËÉΩÂ∞ë‰∫é6‰Ωç";
      } else {
        this.errors.password = "";
      }
      if (this.form.confirmPassword) {
        this.validateConfirmPassword();
      }
    },
    validateConfirmPassword() {
      if (!this.form.confirmPassword) {
        this.errors.confirmPassword = "ËØ∑Á°ÆËÆ§ÂØÜÁ†Å";
      } else if (this.form.confirmPassword !== this.form.password) {
        this.errors.confirmPassword = "‰∏§Ê¨°ËæìÂÖ•ÁöÑÂØÜÁ†Å‰∏ç‰∏ÄËá¥";
      } else {
        this.errors.confirmPassword = "";
      }
    },
    validateBirthday() {
      if (!this.form.birthday) {
        this.errors.birthday = "ËØ∑ÈÄâÊã©ÁîüÊó•";
      } else {
        const birthDate = new Date(this.form.birthday);
        const today = new Date();
        if (birthDate > today) {
          this.errors.birthday = "ÁîüÊó•‰∏çËÉΩÊôö‰∫é‰ªäÂ§©";
        } else {
          this.errors.birthday = "";
        }
      }
    },
    handleRegister() {
      this.validatePhone();
      this.validatePassword();
      this.validateConfirmPassword();
      this.validateBirthday();
      
      if (this.isFormValid) {
        console.log("Ê≥®ÂÜå‰ø°ÊÅØÔºö", this.form);
        // ËøôÈáåÂêéÁª≠‰ºöÊ∑ªÂä†ÂÆûÈôÖÁöÑÊ≥®ÂÜåÈÄªËæë
      }
    }
  }
};
</script>

<style lang="scss" scoped>
// ... ‰øùÊåÅÂéüÊúâÊ†∑Âºè ...

.error-message {
  color: #ff4d4f;
  font-size: 12px;
  margin-top: 4px;
  display: block;
}

